@import "~materialize-css/dist/css/materialize.min.css";


.socialAuthContainer {
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
}

.mainContainer {
    margin-top: 15vh;
}

.message span {
    opacity: 0.9;
    font-size: 12px;
    color: #2b4740;
}

.message p {
    display: inline-flex;
    max-width: 420px;
    border-radius: 10px;
    border: 1px solid rgba(0, 0, 0, 0.1);
    background-color: #28574f;
    padding: 10px 15px;
    color: #fff;
    margin-bottom: 2px;
    opacity: 0.9;
    word-break:break-all;
    box-shadow: 0 0 5px #83c6dc;
}

.messages {
    padding-left: 15px;
    overflow: auto;
    height: 40vh;
    margin-left: 10px;
    margin-right: 10px;
    border-radius: 5px;
    border: 1px #064531;
    box-shadow: 0 2px 15px #83c6dc;
}

.buttonContainer {

    margin: 10px 10px;
    display: flex;
    justify-content: flex-start;
}

.mainBlock {
    margin-top: 10vh;
    border-radius: 5px;
    padding-bottom: 5px;
}